@import "vars";

:root {
  --prority-one: #46877f;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;

  &:before,
  &:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  &:focus {
    outline: none;
  }
}

*::-webkit-scrollbar {
  width: 5px;
}

*::-webkit-scrollbar-track {
  box-shadow: inset 0 0 4px var(--prority-one);
}

*::-webkit-scrollbar-thumb {
  background: var(--prority-one);
  // outline: 1px solid slategrey;
}

html.hidden {
  overflow: hidden;
}

html {
  font-size: 10px;
}

html,
body {
  height: 100%;
}

body {
  min-width: 320px;
  position: relative;
  font-family: 'Noto Sans', sans-serif;
  font-weight: 400;
  overflow-x: hidden;
  color: #5E5E5E;
  background-color: #fff;
}

a {
  color: inherit;
}

select,
a,
button,
textarea,
input {
  cursor: pointer;
  outline: none;
}

textarea {
  resize: none;
}

a {
  text-decoration: none;
  color: inherit;

  &:hover {
    text-decoration: none;
    color: inherit;
  }
}
main {
  margin-top: 115px;
}
.container {
  padding: 0 40px;
  width: 1280px;
  max-width: 100%;
  display: flex;
  margin: 0 auto;
  color: #000;

  &::after {
    display: none;
  }

  &:before {
    display: none;
  }
}

.col-6 {
  width: 50%;
  padding: 0 20px;
}

.wp-text {
  .more-txt {
    margin-bottom: 10px;
  }
  .btn-more {
    margin-top: 5px;
    font-size: 18px;
    text-transform: capitalize;
    color: var(--prority-one);
    text-decoration: underline;
    opacity: 1;
    transition: $transition;
    &:hover {
      opacity: .85;
      text-decoration: none;
      transition: $transition;
    }
  }
  .btn-less {
    font-size: 18px;
    text-transform: capitalize;
    color: var(--prority-one);
    text-decoration: underline;
    opacity: 1;
    transition: $transition;
    &:hover {
      opacity: .85;
      text-decoration: none;
      transition: $transition;
    }
  }
  h1, h2, h3, h4, h5, h6 {
    font-weight: 400;
    color: #000100;
    b {
      font-weight: 700;
    }
  }
  h1 {
    font-size: 4.8rem;
    line-height: 5.5rem;
    color: #000100;
    margin: 20px 0;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  h2 {
    font-size: 3.6rem;
    line-height: 4.9rem;
    margin: 20px 0;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  h3 {
    font-size: 3rem;
    line-height: 4rem;
    margin: 20px 0;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  h4 {
    font-size: 2.2rem;
    line-height: 3.2rem;
    margin: 20px 0;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  h5 {
    font-size: 1.9rem;
    line-height: 2.9rem;
    margin: 20px 0;
    color: #5E5E5E;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  h6 {
    font-size: 1.6rem;
    line-height: 2.6rem;
    color: #5E5E5E;
    margin: 15px 0;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  p {
    font-size: 1.4rem;
    line-height: 2.4rem;
    color: #5E5E5E;
    margin: 15px 0;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.title-section {
  font-size: 3.6rem;
  line-height: 4.9rem;
  margin: 0 0 20px 0;
  padding: 0 20px;
  font-weight: 400;
  color: #000100;
}

.img-bg {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
}
.img-sm {
  display: none;
}

.grid {
  display: grid;
}